/* Squad */
@font-face {
    font-family: "Squad";
    font-style: normal;
    font-display: swap;
    font-weight: 400;
    src: url("../fonts/Squad-Regular.woff2") format("woff2"), url("../fonts/Squad-Regular.woff") format("woff"), local("Roboto");
}

@font-face {
    font-family: "Squad";
    font-style: normal;
    font-display: swap;
    font-weight: 700;
    src: url("../fonts/Squad-Bold.woff2") format("woff2"), url("../fonts/Squad-Bold.woff") format("woff"), local("Roboto");
}

@font-face {
  font-family: "Squad";
  src: url("../fonts/Squad-SemiBold.woff2") format("woff2"),
    url("../fonts/Squad-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

