@charset "UTF-8";
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --container-width: 1360px;
  --container-padding: 20px;
  --plashki: #424e59;
  --izobrazhenie: #d2dce8;
  --fon-plashek: #eaedf2;
  --temnyy: #080b0f;
  --tekst: #003649;
  --tekst-svetlyy: #405f6a;
  --belyy: #fff;
  --tegi: #556473;
  --bg-svetlo-seryy: #f9f9f9;
  --color-temno-zelenyy: #206175;
  --color-zelenyy: #3d7c85;
  --color-svetlo-zelenyy-1: #bedbd7;
  --color-svetlo-zelenyy-2: rgba(190, 219, 215, 0.4);
  --gradient-green: linear-gradient(222deg, rgba(106,165,174,1) 0%, rgba(61,124,133,1) 100%);
  --gradient-light-gray: linear-gradient(222deg, rgba(247,250,255,1) 0%, rgba(234,237,242,1) 100%);
  --gradient-for-photo-decor: linear-gradient(0deg, rgba(32,97,117,1) 0%, rgba(32,97,117,0) 30%, rgba(32,97,117,0) 100%);
  --decor-gray: #C7D6DC;
  --font-family: "Squad", sans-serif;
  --temnyy: #3c3232;
  --osnovnoy: #4285f4;
  --akcent: #ed4545;
}

/* Squad */
@font-face {
  font-family: "Squad";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/Squad-Regular.woff2") format("woff2"), url("../fonts/Squad-Regular.woff") format("woff"), local("Roboto");
}
@font-face {
  font-family: "Squad";
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: url("../fonts/Squad-Bold.woff2") format("woff2"), url("../fonts/Squad-Bold.woff") format("woff"), local("Roboto");
}
@font-face {
  font-family: "Squad";
  src: url("../fonts/Squad-SemiBold.woff2") format("woff2"), url("../fonts/Squad-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
/* Общие glob */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

body {
  margin: 0;
  overflow-y: auto;
  padding: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;
  color: #0D2436;
  font-family: var(--font-family);
  background-color: #fff;
}
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  pointer-events: none;
}
body.overlay-active::before {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.container {
  width: 100%;
  max-width: 1231px;
  margin: 0 auto;
  padding: 0 15px;
}

.section-offset {
  margin-bottom: 135px;
}
@media (max-width: 768px) {
  .section-offset {
    margin-bottom: 40px;
  }
}

.section-offset-gray {
  padding: 0 0 80px 0;
  background-color: #F8F8F8;
}
@media (max-width: 768px) {
  .section-offset-gray {
    display: none;
  }
}

.section-padding {
  padding: 160px 0;
  background: rgba(66, 133, 244, 0.1);
}

.section__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 50px;
}

.section__row {
  display: flex;
  gap: 40px;
}
.section__row > div {
  width: 100%;
  max-width: calc(50% - 20px);
}

@media (max-width: 1100px) {
  .section__row {
    flex-direction: column;
    gap: 20px;
  }
  .section__row > div {
    max-width: 100%;
  }
}
.section-columns {
  display: flex;
  gap: 40px;
}
.section-columns .container {
  display: contents;
}
.section-columns__left {
  flex-grow: 1;
  max-width: calc(100% - 340px);
}
.section-columns__right {
  flex: 0 0 300px;
}

@media (max-width: 1024px) {
  .section-columns {
    flex-direction: column-reverse;
    gap: 80px;
  }
  .section-columns__left {
    max-width: 100%;
  }
  .section-columns__right {
    flex: 1 1 auto;
  }
  .container {
    overflow: hidden;
  }
}
@media (max-width: 768px) {
  .section-columns {
    gap: 40px;
  }
}
.img-cover {
  width: 100%;
  max-height: 100%;
  object-fit: cover;
  object-position: top;
}

.section-head {
  padding: 30px 0;
}

.section-head-white {
  padding: 40px 0 40px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 100px;
}
.section-head-white .breadcrumbs li,
.section-head-white .breadcrumbs li a {
  color: rgba(60, 50, 50, 0.8);
}
.section-head-white .breadcrumbs li a:hover {
  color: rgb(60, 50, 50);
}
.section-head-white .breadcrumbs li:after {
  color: rgba(60, 50, 50, 0.8);
}
.section-head-white .title-h1 {
  color: var(--temnyy);
  text-align: left;
}

/*  buttons */
.btn-transparent {
  font-weight: 500;
  font-size: 17px;
  line-height: 100%;
  border-radius: 5px;
  border: 1px solid #E6E6E6;
  padding: 14px 21px;
  color: #515151;
}

.btn-orange {
  font-weight: 500;
  font-size: 17px;
  line-height: 100%;
  max-width: 230px;
  width: 100%;
  border-radius: 5px;
  background-color: #EF8459;
  padding: 14px 24px;
  color: #FFFFFF;
}

.btn-green {
  display: inline-flex;
  white-space: nowrap;
  font-weight: 500;
  font-size: 17px;
  line-height: 100%;
  border-radius: 5px;
  background-color: #A6C68D;
  padding: 14px 24px;
  color: #FFFFFF;
}
@media (max-width: 1023px) {
  .btn-green {
    white-space: wrap;
  }
}

.btn-text-green {
  display: flex;
  gap: 2px;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  max-width: 149px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #E6E6E6;
  padding: 18px;
  color: #A6C68D;
}

.btn-red {
  padding: 10px 25px;
  font-weight: 600;
  line-height: 25px;
  font-size: 16px;
  background-color: #EA1414;
  border-radius: 5px;
  color: #ffffff;
}

.btn-white {
  display: block;
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  text-align: center;
  border-radius: 2px;
  padding: 20px 40px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: all 0.3s ease;
  background-color: #FFFFFF;
  color: #EF8459;
  border: 1px solid transparent;
}
.btn-white:hover {
  background: transparent;
  color: #FFFFFF;
  border-color: #FFFFFF;
}

.btn-more {
  display: flex;
  align-items: center;
  gap: 15px;
  font-weight: 500;
  font-size: 20px;
  line-height: 130%;
  color: #515151;
  text-align: left;
  transition: all 0.3s ease;
}
.btn-more:hover {
  opacity: 0.5;
}
.btn-more.active::after {
  transform: rotate(315deg);
}

/*  titles  */
.title-h1,
.title-h2 {
  font-family: var(--font-family);
  font-weight: 700;
  line-height: 110%;
  color: #0D2436;
}

.title-h1 {
  font-weight: 700;
  font-size: 42px;
  line-height: 45px;
  color: #2E2F45;
  margin-bottom: 40px;
}

.title-h2 {
  font-size: 42px;
  font-weight: 700;
  position: relative;
  margin-bottom: 40px;
}

.title-h3 {
  font-weight: 700;
  font-size: 28px;
  line-height: 35px;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .title-h3 {
    font-size: 20px;
    line-height: 25px;
  }
}

/*  form  */
.input {
  background: #E9EFFD;
  border-radius: 0;
  padding: 15px 15px 15px 20px;
  line-height: 130%;
  color: rgba(13, 36, 54, 0.5);
  border-bottom: 1px solid #fff;
  transition: all 0.3s ease;
  border-radius: 10px;
}

.input::placeholder {
  color: rgba(13, 36, 54, 0.5);
}

.input-black {
  padding: 20px 0;
  color: rgb(60, 50, 50);
  border-bottom: 1px solid var(--temnyy);
}

.input-black::placeholder {
  color: rgba(60, 50, 50, 0.6);
}

.input-wrapper {
  position: relative;
  overflow: hidden;
}
.input-wrapper .input {
  width: 100%;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.textarea {
  background: var(--belyy);
  border-radius: 5px;
  padding: 16px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: var(--tekst);
  border: 1px solid transparent;
  transition: all 0.3s ease;
  width: 100%;
  min-height: 142px;
  resize: none;
}

.politic {
  display: flex;
  gap: 8px;
  font-size: 14px;
  line-height: 130%;
  color: rgba(13, 36, 54, 0.5);
}
.politic a {
  color: rgba(13, 36, 54, 0.5);
  text-decoration: underline;
}
.politic a:hover {
  color: rgba(13, 36, 54, 0.5);
}
.politic input[type=checkbox] {
  position: relative;
  border: 1.5px solid #fff;
  border-radius: 2px;
  min-width: 20px;
  width: 20px;
  height: 20px;
  color: #000;
  appearance: none;
  outline: 0;
  cursor: pointer;
  transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}
.politic input[type=checkbox]::before {
  position: absolute;
  content: "";
  display: block;
  top: 2.5px;
  left: 5.5px;
  width: 5px;
  height: 10px;
  border-style: solid;
  border-color: #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  opacity: 0;
}
.politic input[type=checkbox]:checked {
  color: #fff;
}
.politic input[type=checkbox]:checked::before {
  opacity: 1;
}

.politic-black {
  color: rgba(60, 50, 50, 0.7);
}
.politic-black a {
  color: rgba(60, 50, 50, 0.7);
}
.politic-black a:hover {
  color: rgb(60, 50, 50);
}
.politic-black input[type=checkbox] {
  border: 1.5px solid var(--osnovnoy);
}
.politic-black input[type=checkbox]::before {
  border-color: var(--osnovnoy);
}
.politic-black input[type=checkbox]:checked {
  color: var(--osnovnoy);
}

/*  search  */
.form-search {
  position: relative;
  width: 100%;
}
.form-search input {
  border: 1px solid rgba(237, 69, 69, 0.5);
  border-radius: 2px;
  padding: 20px 22px;
}
.form-search input::placeholder {
  color: rgba(60, 50, 50, 0.6);
}
.form-search input:focus {
  border: 1px solid var(--akcent);
}
.form-search img {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
}

/* select */
select {
  width: 100%;
  max-width: 100%;
  cursor: pointer;
  outline: none;
  border: 1px solid rgba(66, 133, 244, 0.5);
  border-radius: 2px;
  padding: 18px;
  color: var(--temnyy);
  padding-right: 36px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background-position-x: calc(100% - 18px);
  transition: all 0.3s ease;
}

.sort_block label {
  display: block;
  font-size: 16px;
  line-height: 130%;
  color: rgba(60, 50, 50, 0.6);
  margin-bottom: 12px;
}

/* accordion */
.accordion {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  cursor: pointer;
  width: 100%;
  position: relative;
}
.accordion--active .accordion__btn-more {
  transform: rotate(180deg);
}
.accordion__content {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

/* tabs */
.tab-btns {
  display: flex;
  gap: 10px;
}

.tab-btn {
  border-radius: 2px;
  padding: 16px 32px;
  background: rgba(66, 133, 244, 0.05);
  border: 1px solid transparent;
  font-weight: 500;
  transition: all 0.3s ease;
}
.tab-btn:hover {
  color: #FFFFFF;
}
.tab-btn.active {
  border: none;
  color: #FFFFFF;
  background-color: #84A4E4;
}

.tab-content {
  display: none;
}
.tab-content.active {
  display: block;
  width: 100%;
  min-height: 100%;
}

/* breadcrumbs */
.breadcrumbs {
  position: relative;
  z-index: 2;
}

.breadcrumbs,
.breadcrumbs li {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 6px;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.breadcrumbs a {
  font-size: 14px;
  line-height: 130%;
  color: rgba(13, 36, 54, 0.5);
  transition: all 0.2s ease-in;
  font-weight: 400;
}

.breadcrumbs li {
  font-size: 14px;
  line-height: 130%;
  font-weight: 500;
  transition: all 0.2s ease-in;
}

.breadcrumbs li:after {
  content: "/";
  display: block;
  font-size: 20px;
  color: #828282;
  font-size: inherit;
}

.breadcrumbs li:last-child::after {
  content: none;
}

.columns-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}

.columns-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

.btn-show {
  width: 100%;
  max-width: 300px;
  margin: 40px auto 0;
}

.article__items {
  display: flex;
  gap: 60px;
  margin-top: 30px;
}
.article__items li {
  display: flex;
  gap: 12px;
}
.article__items li::before {
  content: "";
  display: block;
  min-width: 24px;
  width: 24px;
  height: 24px;
  background: url("./../img/icons/calendar-blue.svg") no-repeat center/contain;
}
.article__items li:first-child::before {
  background: url("./../img/icons/eye-blue.svg") no-repeat center/contain;
}

/* rating */
.full-stars {
  text-align: center;
}

.full-stars .rating-group {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.full-stars input {
  position: absolute;
  left: -9999px;
}

.full-stars label {
  margin: 0;
  cursor: pointer;
}

.full-stars label svg {
  margin: 0 2px;
  height: 32px;
  width: 33px;
  fill: rgba(234, 20, 20, 0.5);
  -webkit-transition: fill 0.3s;
  -o-transition: fill 0.3s;
  transition: fill 0.3s;
}

.full-stars input:checked ~ label svg {
  fill: transparent;
}

.full-stars .rating-group:hover label svg {
  fill: rgba(234, 20, 20, 0.5);
}

.full-stars .rating-group input:hover ~ label svg {
  fill: transparent;
}

/*  media global  */
@media (max-width: 1100px) {
  .columns-3 {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
@media (max-width: 1024px) {
  .title-h1 {
    font-size: 38px;
  }
  .title-h2 {
    font-size: 30px;
    margin: 0 0 30px 0;
  }
}
@media (max-width: 930px) {
  .columns-2 {
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }
}
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
  .section-offset {
    margin-bottom: 40px;
  }
  .section-padding {
    padding: 80px 0;
  }
  .section-head {
    padding: 15px 0 20px;
  }
  .section-head-white {
    padding: 20px 0;
    margin-bottom: 40px;
  }
  .title-h1 {
    font-size: 28px;
  }
  .title-h2 {
    font-size: 24px;
  }
  .btn-blue,
  .btn-blue-white,
  .btn-red,
  .btn-red-white,
  .btn-white {
    font-size: 16px;
  }
  .input {
    padding-bottom: 12px;
  }
  .tab-btns {
    gap: 6px;
  }
  .tab-btn {
    padding: 14px 16px;
  }
  .breadcrumbs {
    margin-bottom: 0;
  }
  .breadcrumbs,
  .breadcrumbs li {
    gap: 4px;
  }
  .breadcrumbs li,
  .breadcrumbs li a {
    font-size: 14px;
  }
  .article__items {
    flex-direction: column;
