/* swiper-pagination  */
.swiper-pagination {
  position: static;
  width: auto !important;
}
.swiper-pagination-bullet {
  position: static;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background: #84A4E4;
}

.swiper-number {
  width: 52px;
  height: 52px;
  border-radius: 100px;
  font-weight: 700;
  font-size: 24px;
  line-height: 110%;
  color: #fff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease-in;
}

.swiper-pagination-fraction {
  font-weight: 700;
  font-size: 24px;
  color: #0D2436;
}
.swiper-pagination-fraction .swiper-pagination-current {
  font-weight: 700;
  font-size: 45px;
  line-height: 50px;
  color: #84A4E4;
}

.swiper-scrollbar {
  position: static !important;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}
.swiper-scrollbar-drag {
  background: var(--belyy);
  border-radius: 10px;
}

.swiper-scrollbar-temnyy {
  position: static !important;
